

function change(el) {
        if (el == 'boxes') {
            var a = document.getElementById('boxes');
            a.style.color = "#0867a6";
        }
        if (el == 'filter') {
            var a = document.getElementById('filter');
            a.style.color = "#0867a6";
        }
    };




